// Base API configuration
const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000'

export class ApiError extends Error {
  constructor(
    message: string,
    public status: number,
    public data?: any
  ) {
    super(message)
    this.name = 'ApiError'
  }
}

async function fetchApi<T>(
  endpoint: string,
  options?: RequestInit
): Promise<T> {
  const url = `${API_BASE_URL}${endpoint}`
  
  const response = await fetch(url, {
    ...options,
    headers: {
      'Content-Type': 'application/json',
      ...options?.headers,
    },
  })

  if (!response.ok) {
    const errorData = await response.json().catch(() => ({}))
    throw new ApiError(
      errorData.message || 'API request failed',
      response.status,
      errorData
    )
  }

  return response.json()
}

// Types
export interface Task {
  task_id: string
  description: string
  status: string
  created_at: string
  updated_at: string
  current_stage: string
  progress_percent: number
  plan_version?: number
  current_step?: {
    step_id: string
    step_number: number
    description: string
    status: string
    started_at?: string
  }
  total_steps: number
  completed_steps: number
  latest_logs: Array<{
    timestamp: string
    stage: string
    model: string
    content_preview: string
  }>
  agents_in_use: string[]
  tools_in_use: string[]
}

export interface TasksResponse {
  tasks: Task[]
  total: number
}

export interface Plan {
  id: string
  task_id: string
  version: number
  goal: string
  strategy?: string | {
    approach?: string
    assumptions?: string
    constraints?: string
    success_criteria?: string
  }
  steps: PlanStep[]
  alternatives?: any[]
  status: string
  current_step?: number
  estimated_duration?: number
  actual_duration?: number
  created_at: string
  approved_at?: string
}

export interface PlanStep {
  step_id?: string
  description: string
  status?: string
  assigned_agent?: string
}

export interface Agent {
  id: string
  name: string
  description?: string
  status: string
  capabilities?: string[]
  version: number
  created_at: string
  updated_at: string
  total_tasks_executed: number
  successful_tasks: number
  failed_tasks: number
}

export interface Message {
  id: string
  task_id?: string
  role: 'user' | 'assistant' | 'system'
  content: string
  created_at: string
  session_id?: string
  response?: string
}

export interface Prompt {
  id: string
  name: string
  prompt_text: string
  prompt_type: string
  level: number
  version: number
  status: string
  success_rate?: number
  usage_count: number
  created_at: string
  parent_prompt_id?: string
  avg_execution_time?: number
  user_rating?: number
}

export interface PromptMetrics {
  prompt_id: string
  prompt_name: string
  version: number
  usage_count: number
  success_rate?: number
  avg_execution_time?: number
  user_rating?: number
  usage_history: any[]
  total_history_entries: number
}

export interface AgentMemory {
  id: string
  agent_id: string
  memory_type: string
  content: any
  summary?: string
  importance: number
  access_count: number
  last_accessed_at?: string
  tags?: string[]
  source?: string
  created_at: string
  expires_at?: string
}

export interface AgentContext {
  id: string
  agent_id: string
  session_id?: string
  context_key: string
  content: any
  ttl_seconds?: number
  created_at: string
  expires_at?: string
}

// API Client
export const api = {
  // Tasks (using current-work endpoint)
  tasks: {
    list: () => fetchApi<TasksResponse>('/api/current-work/tasks').then(res => res.tasks),
    get: (id: string) => fetchApi<Task>(`/api/current-work/task/${id}`),
  },

  // Plans
  plans: {
    list: (taskId?: string) => {
      // Ensure taskId is a string, not an object
      if (taskId !== undefined && taskId !== null) {
        if (typeof taskId !== 'string') {
          console.error('plans.list received non-string taskId:', taskId, typeof taskId)
          // If it's an object, try to extract id
          if (typeof taskId === 'object') {
            const id = (taskId as any)?.id || (taskId as any)?.task_id
            if (id && typeof id === 'string') {
              return fetchApi<Plan[]>(`/api/plans?task_id=${id}`)
            }
          }
          // Return all plans if invalid taskId
          return fetchApi<Plan[]>('/api/plans')
        }
        // Valid string taskId
        return fetchApi<Plan[]>(`/api/plans?task_id=${taskId}`)
      }
      // No taskId provided
      return fetchApi<Plan[]>('/api/plans')
    },
    get: (id: string) => fetchApi<Plan>(`/api/plans/${id}`),
    create: (taskId: string) =>
      fetchApi<Plan>('/api/plans', {
        method: 'POST',
        body: JSON.stringify({ task_id: taskId }),
      }),
  },

  // Agents
  agents: {
    list: () => fetchApi<Agent[]>('/api/agents'),
    get: (id: string) => fetchApi<Agent>(`/api/agents/${id}`),
    create: (data: {
      name: string
      description?: string
      system_prompt?: string
      capabilities?: string[]
      model_preference?: string
      temperature?: string
      max_concurrent_tasks?: number
      tags?: string[]
    }) =>
      fetchApi<Agent>('/api/agents', {
        method: 'POST',
        body: JSON.stringify(data),
      }),
    update: (id: string, data: {
      description?: string
      system_prompt?: string
      capabilities?: string[]
      model_preference?: string
      temperature?: string
      max_concurrent_tasks?: number
      tags?: string[]
    }) =>
      fetchApi<Agent>(`/api/agents/${id}`, {
        method: 'PUT',
        body: JSON.stringify(data),
      }),
    activate: (id: string) =>
      fetchApi<Agent>(`/api/agents/${id}/activate`, {
        method: 'POST',
      }),
    pause: (id: string) =>
      fetchApi<Agent>(`/api/agents/${id}/pause`, {
        method: 'POST',
      }),
    deprecate: (id: string) =>
      fetchApi<Agent>(`/api/agents/${id}/deprecate`, {
        method: 'POST',
      }),
  },

  // Messages/Chat
  chat: {
    sendMessage: (taskId: string, content: string, sessionId?: string) => {
      // taskId can be 'general' for general chat or actual task ID
      const body: any = {
        message: content,
        task_type: 'general_chat',
      }
      if (sessionId) {
        body.session_id = sessionId
      }
      return fetchApi<{
        response: string
        model: string
        task_type: string
        duration_ms?: number
        session_id?: string
        trace_id?: string
      }>('/api/chat', {
        method: 'POST',
        body: JSON.stringify(body),
      })
    },
    getHistory: (taskId: string) => {
      // For now, return empty array as chat history is session-based
      // TODO: Implement proper session management
      return Promise.resolve([] as Message[])
    },
    getSession: (sessionId: string) =>
      fetchApi<{ id: string; messages: Message[] }>(`/api/chat/session/${sessionId}`),
  },

  // Prompts
  prompts: {
    list: (params?: {
      prompt_type?: string
      status?: string
      level?: number
      name?: string
      limit?: number
      offset?: number
    }) => {
      const queryParams = new URLSearchParams()
      if (params?.prompt_type) queryParams.append('prompt_type', params.prompt_type)
      if (params?.status) queryParams.append('status', params.status)
      if (params?.level !== undefined) queryParams.append('level', String(params.level))
      if (params?.name) queryParams.append('name', params.name)
      if (params?.limit) queryParams.append('limit', String(params.limit))
      if (params?.offset) queryParams.append('offset', String(params.offset))
      
      const query = queryParams.toString()
      return fetchApi<Prompt[]>(`/api/prompts${query ? `?${query}` : ''}`)
    },
    get: (id: string) => fetchApi<Prompt>(`/api/prompts/${id}`),
    create: (data: {
      name: string
      prompt_text: string
      prompt_type: string
      level?: number
    }) =>
      fetchApi<Prompt>('/api/prompts', {
        method: 'POST',
        body: JSON.stringify(data),
      }),
    update: (id: string, data: {
      name?: string
      prompt_text?: string
      status?: string
    }) =>
      fetchApi<Prompt>(`/api/prompts/${id}`, {
        method: 'PUT',
        body: JSON.stringify(data),
      }),
    delete: (id: string) =>
      fetchApi<{ status: string; prompt_id: string }>(`/api/prompts/${id}`, {
        method: 'DELETE',
      }),
    createVersion: (id: string, prompt_text: string) =>
      fetchApi<Prompt>(`/api/prompts/${id}/version`, {
        method: 'POST',
        body: JSON.stringify({ prompt_text }),
      }),
    getVersions: (id: string) => fetchApi<Prompt[]>(`/api/prompts/${id}/versions`),
    getMetrics: (id: string) => fetchApi<PromptMetrics>(`/api/prompts/${id}/metrics`),
    deprecate: (id: string) =>
      fetchApi<Prompt>(`/api/prompts/${id}/deprecate`, {
        method: 'POST',
      }),
  },

  // Agent Memory
  agentMemory: {
    list: (agentId: string, params?: {
      memory_type?: string
      min_importance?: number
      tags?: string
      limit?: number
    }) => {
      const queryParams = new URLSearchParams()
      if (params?.memory_type) queryParams.append('memory_type', params.memory_type)
      if (params?.min_importance !== undefined) queryParams.append('min_importance', String(params.min_importance))
      if (params?.tags) queryParams.append('tags', params.tags)
      if (params?.limit) queryParams.append('limit', String(params.limit))
      
      const query = queryParams.toString()
      return fetchApi<AgentMemory[]>(`/api/agents/${agentId}/memory${query ? `?${query}` : ''}`)
    },
    get: (agentId: string, memoryId: string) =>
      fetchApi<AgentMemory>(`/api/agents/${agentId}/memory/${memoryId}`),
    create: (agentId: string, data: {
      memory_type: string
      content: any
      summary?: string
      importance?: number
      tags?: string[]
      source?: string
    }) =>
      fetchApi<AgentMemory>(`/api/agents/${agentId}/memory`, {
        method: 'POST',
        body: JSON.stringify(data),
      }),
    update: (agentId: string, memoryId: string, data: {
      content?: any
      summary?: string
      importance?: number
      tags?: string[]
    }) =>
      fetchApi<AgentMemory>(`/api/agents/${agentId}/memory/${memoryId}`, {
        method: 'PUT',
        body: JSON.stringify(data),
      }),
    delete: (agentId: string, memoryId: string) =>
      fetchApi<{ status: string; memory_id: string }>(`/api/agents/${agentId}/memory/${memoryId}`, {
        method: 'DELETE',
      }),
    search: (agentId: string, query: {
      query_text?: string
      content_query?: any
      memory_type?: string
      limit?: number
    }) =>
      fetchApi<AgentMemory[]>(`/api/agents/${agentId}/memory/search`, {
        method: 'POST',
        body: JSON.stringify(query),
      }),
    // Short-term memory (context)
    saveContext: (agentId: string, data: {
      context_key: string
      content: any
      session_id?: string
      ttl_seconds?: number
    }) =>
      fetchApi<AgentContext>(`/api/agents/${agentId}/context`, {
        method: 'POST',
        body: JSON.stringify(data),
      }),
    getContext: (agentId: string, contextKey: string) =>
      fetchApi<AgentContext>(`/api/agents/${agentId}/context/${contextKey}`),
    getAllContext: (agentId: string) =>
      fetchApi<AgentContext[]>(`/api/agents/${agentId}/context`),
    clearContext: (agentId: string) =>
      fetchApi<{ status: string; cleared_count: number }>(`/api/agents/${agentId}/context`, {
        method: 'DELETE',
      }),
  },
}
